.ad-container {
  background-color: rgb(148, 149, 149);
  height: 93%;
  color: black;
}
  
.ad-image {
  object-fit: cover;
  width: 100%;
  height: 100%;
}
  
.ad-close-button {
  position: absolute;
  top: 1rem;
  right: 1rem;
  width: 2rem;
  height: 2rem;
  background-color: black;
  border-radius: 50%;
  border: none;
  cursor: pointer;
  display: flex;
  align-items: center;
  justify-content: center;
  transition: background-color 0.3s ease;
}
.cross-icon {
  width: 1rem; /* 叉叉的大小 */
  height: 1rem;
}
.ad-close-button:hover {
  background-color: rgba(0, 0, 0, 0.5);
}  
  

.ad-message {
  font-size: 1rem;
  top: 2rem;
  z-index: 5;
  text-align: center;
}

.ad-close-info {
  font-size: 1rem;
  color: white;
  background-color: #575454;
  text-align: center;
  width: 100%;
  height: 7%;
}
  